<template>
	<view class="box">
		<view>
			<!-- 提示信息弹窗 -->
			<uni-popup ref="message" type="message">
				<uni-popup-message :type="msgType" :message="messageText" :duration="2000"></uni-popup-message>
			</uni-popup>
		</view>
		<view class="header" v-if="!isedit">
			<view class="top" >
				{{dataList.title }}
			</view>
			<view class="bottom">
				<text>{{dataList.date | dateFormat}}</text>
				<text style="margin: 0 20rpx;">|</text>
				<text>&nbsp;{{length}}字</text>
			</view>
		</view>
		<view class="main" v-if="!isedit">
			{{dataList.content}}
		</view>
		<view class="text-area" v-if="isedit">
			<view class="login-img">
				<view class="owl-login" :class="hideEyes?'password':''">
					<view class="hand"></view>
					<view class="hand hand-r"></view>
					<view class="arms">
						<view class="arm"></view>
						<view class="arm arm-r"></view>
					</view>
				</view>
			</view>
			<view class="textarea_from">
				<u--form :model="loginForm" ref="uLoginForm">
					<view>
						<text class="title">编辑备忘</text>
						<br />
						<p style="font-weight: bold;margin-top: 8px;">{{dataList.date | dateFormat}}</p>
					</view>
					<u-form-item prop="title">
						<u-input v-model="loginForm.title" placeholder="备忘主题" style="background-color: white" />
					</u-form-item>
					<u-form-item prop="content">
						<u--textarea v-model="loginForm.content" @focus="passwordF_B" @blur="passwordF_B" height=160
							count :maxlength="maxlength" placeholder="请输入备忘内容" border="bottom"></u--textarea>
					</u-form-item>
					<view class="btn_Box">
						<button type="primary" class="submitBtn" @click="add()">保存更改</button>
						<button type="warn" class="resetBtn" @click="resetFrom()">备忘重置</button>
					</view>
				</u--form>
			</view>
			
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				dataList:[],
				id:"",
				length:0,
				isedit:false,
				hideEyes: false,
				height: 290,
				maxlength: 10000,
				type: 'center',
				msgType: 'success',
				messageText: '这是一条成功提示',
				loginForm: {
					content: "",
					title: ""
				},
			};
		},
		onLoad(e) {
			console.log(e.id)
			this.id=e.id
			this.getmemo(e.id)
		},
		mounted() {
			    },
		
		methods:{
			messageToggle(type,zxl) {
				this.msgType = type
				this.messageText = zxl
				this.$refs.message.open()
			},
			async add() {
							if(this.loginForm.title.trim()==''){
								uni.showToast({
									icon:"none",
									title:'请填写备忘主题'
								})
							}
							else if(this.loginForm.content.trim()==''){
								uni.showToast({
									icon:"none",
									title:'请填写备忘内容'
								})
							}
							else{
						   let res=await this.$zxl('/editmemo',{
						   			content: this.loginForm.content,
						   			title: this.loginForm.title,
				                    id:this.id
						   		},'POST')
								if (res.data.status==200) {
									this.messageToggle('success','更改成功')
									this.resetFrom()
									this.getmemo(this.id)
									this.isedit=!this.isedit
									var webView = this.$mp.page.$getAppWebview();
									webView.setTitleNViewButtonStyle(0,{  
										text:'编辑'
									});
								}
							}	
						},
						resetFrom() {
							this.loginForm.title=''
							this.loginForm.content=''
						},
						passwordF_B() {
							this.hideEyes = !this.hideEyes;
						},
						formSubmit(e) {
							console.log(e.detail.value);
						},
			onNavigationBarButtonTap(e){
			 this.isedit=!this.isedit
			 var webView = this.$mp.page.$getAppWebview();
			 webView.setTitleNViewButtonStyle(0,{  
			 	text:this.isedit?'完成':'编辑'
			 });
			},
			async getmemo(id){
				let res=await this.$zxl('/getmemoid',{id}) 
					this.dataList=res.data.data[0]
					this.length=res.data.data[0].content.length
					this.loginForm.content=res.data.data[0].content
					this.loginForm.title=res.data.data[0].title
					
					console.log(res.data.data[0],'备忘录')
			}
		}
	}
</script>
<style>
	page {
		background:url("../../static/images/bg/8.jpg");
		background-repeat: no-repeat;
		background-size: cover;
		background-attachment: fixed;
		
	}
</style>
<style lang="less">
	
	.main{
		position: relative;
		margin-top: 20rpx;
		font-size: 40rpx;
		padding: 20rpx;
	}
.header{
		width: 750rpx;
		// height: 100rpx;
		box-sizing: border-box;
		// position: fixed;
		padding: 10rpx 30rpx;
		z-index: 55;
		// top:0; 
		// left: 0;
		background-color: whitesmoke;
		.top{
			font-size: 50rpx;
			font-weight: 900;
			// display: block;
			// white-space: nowrap;
			// overflow: hidden;
			// text-overflow: ellipsis;
		}
		text{
			color: #333;
		}
		}
		.warp {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
		}
		
		.rect {
			width: 220px;
			height: 80px;
			background-color: #F0F0F0;
			border-radius: 25px;
		}
		
		.btn_yes,
		.btn_no {
			margin: 5px;
			padding: 5px 10px;
			display: inline;
		}
		
		.login_register {
			text-align: center;
			margin-top: 5px;
		}
		
		.login-img {
			display: flex;
			justify-content: center;
			align-items: center;
			height: 108px;
			margin-top: 100rpx;
		}
		
		.owl-login {
			width: 211px;
			height: 108px;
			background-image: url('data:image/png;base64,');
			background-repeat: no-repeat;
			background-size: 100%;
			position: absolute;
		}
		
		.owl-login .hand {
			width: 34px;
			height: 34px;
			-webkit-border-radius: 40px;
			border-radius: 40px;
			background-color: #472d20;
			transform: scaleY(0.6);
			transition: 0.3s ease-out;
			position: absolute;
			left: 14px;
			bottom: -8px;
		}
		
		.owl-login .hand.hand-r {
			left: 170px;
		}
		
		.owl-login .arms {
			top: 58px;
			position: absolute;
			width: 100%;
			height: 41px;
			overflow: hidden;
		}
		
		.owl-login .arms .arm {
			width: 40px;
			height: 65px;
			position: absolute;
			left: 20px;
			top: 40px;
			background-image: url("data:image/png;base64,");
			background-size: 100%;
			transition: 0.3s ease-out;
			transform: rotate(-20deg);
		}
		
		.owl-login .arms .arm.arm-r {
			transform: rotate(20deg) scaleX(-1);
			left: 158px;
		}
		
		.owl-login.password .hand {
			transform: translateX(42px) translateY(-15px) scale(0.7);
		}
		
		.owl-login.password .hand.hand-r {
			transform: translateX(-42px) translateY(-15px) scale(0.7);
		}
		
		.owl-login.password .arms .arm {
			transform: translateY(-40px) translateX(40px);
		}
		
		.owl-login.password .arms .arm.arm-r {
			transform: translateY(-40px) translateX(-40px) scaleX(-1);
		}
		
		.textarea_from {
			margin: -9px 10px 0 10px;
			background: #007AFF;
			border-radius: 10px;
			background-color: #EEEEEE;
			box-shadow: 0 2px 10px #9B9B9B;
			padding: 20px;
		
		}
		
		.title {
			font-size: 22px;
			margin-bottom: 5px;
			font-weight: bold;
		
		}
		
		.btn_Box {
			text-align: center;
		}
		
		.submitBtn,
		.resetBtn {
			
			display: inline-block;
			margin: 5px;
		}
</style>
